<template>

    <div>
        <Row>
            <Col span="24">

                <div style="">
                    <h1>{{title_header}}</h1><br><br>
                    <Form :model="formItem" :label-width="80">

                        <Form-item label="">
                            <Radio-group v-model="formItem.radio">
                                <Radio label='y'></Radio>
                                <Radio label='n'></Radio>
                            </Radio-group>
                        </Form-item>
                        <Form-item label="">
                            <Input v-model="formItem.textarea" type="textarea" :autosize="{minRows: 5,maxRows: 5}"
                                   placeholder=""></Input>
                        </Form-item>
                        <Form-item>
                            <Button type="primary" @click="submitInfo()"> </Button>
                        </Form-item>
                    </Form>
                </div>
            </Col>
        </Row>
    </div>

</template>
<script>

    import Cookies from 'js-cookie';
    import qs from "qs";
    import axios from "axios"

    export default {
        data() {
            return {
                title_header: "",
                formItem: {
                    radio: 'y',
                    textarea: '',
                    meetId: ""
                },
            }
        },//data
        created() {
            this.init()
        },
        methods: {
            init() {
                const power = Cookies.get("Admin-Token");
                this.formItem.meetId = Cookies.get("meetId");
                if (power == "admin") {//
                    this.$Message.error(",");
                    this.$router.push({path: '/'});
                } else if (this.formItem.meetId == undefined || this.formItem.meetId == "" || this.formItem.meetId == null) {
                    this.$Message.error("");
                    this.$router.push({path: '/'});
                }
            },
            submitInfo() {
                if (index == 2) {

                }
                const Uname = Cookies.get("UserName");
                const Evaluated = Cookies.get("Evaluated");
                //todo 
                if (Evaluated != (Uname + "_" + this.formItem.meetId)) {//
                    axios.post("/proc/VoteMeeting", qs.stringify(//
                        {
                            id: this.formItem.meetId,
                            userid: Cookies.get("userid"),
                            userexperience: this.formItem.radio == "y" ? 1 : 2,
                            experiencecontent: this.formItem.textarea == "" ? "" : this.formItem.textarea
                        }
                    )).then(response => {
                        if (response.data.ErrCode == -5) {//cookies
                            Cookies.remove('Admin-Token');
                            Cookies.remove('UserName');
                            window.location.reload();
                        }
                        const data0 = response.data;
                        if (data0.ErrCode == -5) {
                            Cookies.remove('Admin-Token');
                            Cookies.remove('UserName');
                            window.location.reload();
                        }
                        if (data0.ErrCode == 0) {
                            this.$Message.success('');
                            //
                            //''+''''''
                            Cookies.set("Evaluated", Uname + "_" + this.formItem.meetId);
                            setTimeout(() => {
                                this.$router.push({path: '/'});
                            }, 1000)
                        } else {
                            this.$Message.error(data0.ErrMsg);
                        }
                    }).catch(error => {
                        console.log(error + "error");
                    });
                } else {
                    this.$Message.error(',');
                }
            },

        }

    }
</script>






